:root {
    --dokan-delivery-time-error: #CC1818;
    --dokan-delivery-time-bg: #FEF0F0;
    --dokan-delivery-time-text: #2E2E2E;
}

.dokan-delivery-time-error {
    border: 1px solid var( --dokan-delivery-time-error );
    background: var( --dokan-delivery-time-bg );
    border-radius: 8px;
    margin-bottom: 10px;

    &-message {
        display: flex;
        color: var( --dokan-delivery-time-text );
        margin: 0 !important;
        padding: 15px;
    }
}

.delivery-time-vendor-select {
    .components-input-control__container {
        background-color: transparent;
    }
    .components-input-control__suffix {
        display: none;
    }
    .components-input-control__backdrop {
        border: none !important;
    }
    select {
        display: block !important;
    }
}

#dokan-delivery-time-box {
    padding: 0 16px;
}

.selector-wrapper {
    flex-wrap: wrap;
}
